// 品牌活动页面

<template>
  <div class="activity pt">
    <DropdownBar />
    <BannerLogo title="品牌活动" :picUrl="bannerurl" />
    <div class="swiper">
        <swiper :options="swiperOption" 
                ref="mySwiper" 
                @tap="gotoActivityDetails()"
                @slideChangeTransitionStart="changeTabsIndex()">
            <swiper-slide v-for="(slide, index) in swiperSlides" :key="index">
                <div class="bannerlink">
                    <img :src="slide.url" alt="banner">
                </div>
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
    <div class="line-width">
        <img src="../../assets/image/before_activity.png" alt="过往活动回顾">
    </div>
    <ContentList v-for="(item, index) in contentListData" :key="index" :initData="item" />
    <FooterBar />
  </div>
</template>

<script>
import DropdownBar from "@/components/DropdownBar";
import FooterBar from "@/components/FooterBar";
import ContentList from "@/components/ContentList";
import BannerLogo from "@/components/BannerLogo";
import { swiper, swiperSlide } from "vue-awesome-swiper";
import "swiper/dist/css/swiper.css";
import Api from "../../api/index.js";
export default {
  name: "activity",
  data() {
    return {
      // 标记当前显示是第几个banner
      tabsIndex: 0,
      bannerurl: require("../../assets/image/activity_logo.png"),
      // 列表数据
      contentListData: [
        // {
        //   type: 0,
        //   url: require("../../assets/image/list1.png"),
        //   describe: "百度图片",
        //   title: "文章标题 111111",
        //   content: "文章内容 1111111",
        //   time: "2018.09.14",
        //   id: 1
        // }
      ],
      swiperOption: {
        loop: true,
        pagination: {
          el: ".swiper-pagination"
        },
        autoplay: {
          delay: 2000,
          stopOnLastSlide: false,
          disableOnInteraction: true
        }
      },
      // 轮播图数据
      swiperSlides: [
        // { url: require("../../assets/image/banner.png", id:11) }
      ]
    };
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  created() {
    this.initPage();
  },
  methods: {
    initPage() {
      Api.getActivityList().then(res => {
        let result = res.data;
        if (result.msg_type == 0) {
          // 获取列表数据
          this.contentListData = JSON.parse(JSON.stringify(result.data.list));
          // 获取轮播图数据
          this.swiperSlides = JSON.parse(JSON.stringify(result.data.banner));
        }
      });
    },
    changeTabsIndex() {
      // 保存当前显示banner的第几页
      this.tabsIndex = this.swiper.activeIndex;
    },
    // 跳转到活动详情页面
    gotoActivityDetails() {
      this.$router.push({
        path: `/activityDetail/${this.swiperSlides[this.tabsIndex].id}`
      });
    }
  },
  components: {
    DropdownBar,
    FooterBar,
    ContentList,
    BannerLogo,
    swiper,
    swiperSlide
  }
};
</script>
<style scoped lang="less">
.bannerlink {
  height: 3.13rem;
  overflow: hidden;
}
.activity {
  background-color: #f2f2f2;
}
.swiper-pagination-fraction,
.swiper-pagination-custom,
.swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 0;
}
</style>
<style>
/* 需要全局覆盖才有效果 */
.swiper-pagination-bullet {
  border: 0.02rem solid #ccc;
  background: transparent;
  width: 0.15rem;
  height: 0.15rem;
  box-sizing: border-box;
  opacity: 1;
}
.swiper-pagination-bullet-active {
  background: #fff;
}
</style>
